import React, { Component } from 'react';
import emailjs from 'emailjs-com';
// init("user_nKeVBfeN3GHyTUJMC2JeR");
class MailBox extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            email: '',
            phone: ''
        }
    }
    handleChange(event) {
        this.setState({ [event.target.name]: event.target.value })
    }
    handleSubmit(event) {
        const templateId = 'template_h6nx0dh';
        this.sendFeedback(templateId, { from_name: this.state.name, reply_to: this.state.email, phone_to: this.state.phone })
    }
    sendFeedback(templateId, variables) {
        emailjs.send(
            "service_yp5zkaq", templateId,
            variables, "user_nKeVBfeN3GHyTUJMC2JeR"
        ).then(res => {
            alert("ng k thnh cng !");
            // console.log('Email successfully sent!')
        })
            .catch(err => alert(err)
                //  console.error('Oh well, you failed. Here some thoughts on the error that occured:', err)
            )

        //rep cus
        // emailjs.send(
        //     "service_yp5zkaq", "template_t1wjple",
        //     variables, "user_nKeVBfeN3GHyTUJMC2JeR"
        // ).then(res => {
        //     console.log('Email successfully sent!')
        // })
        //     // Handle errors here however you like, or use a React error boundary
        //     .catch(err => console.error('Oh well, you failed. Here some thoughts on the error that occured:', err))
    }
    render() {
        return (
            <form className="test-mailing">
                <div className="parent-mail">
                    <div className="top-email"></div>
                    <div className="bottom-email"></div>
                    <div className="box-email container py-3 py-sm-4 px-3 row mx-auto">
                        <div className="text-center" >
                            <div style={{ fontSize: '70px' }}><i className="fas fa-envelope-open-text"></i></div>
                            <h4> ng k nhn tin</h4>
                            <p> Mi thng chng ti u c nhng t gim gi dch v v sn phm nhm chi
                                n khch hng.  c th cp nht kp thi nhng t gim gi ny, vui lng nhp a ch
                                email ca bn vo  di y.</p>
                        </div>
                        <div className="form-group text-center row">
                            <div className="my-2 col-md-4 col-12">
                                <input type="text" className="form-control" required onChange={(e) => this.handleChange(e)} name="name" placeholder="Tn" />
                            </div>
                            <div className=" my-2 col-md-4 col-12">
                                <input type="text" className="form-control" required onChange={(e) => this.handleChange(e)} name="phone" placeholder="in thoi" />
                            </div>
                            <div className=" my-2 col-md-4 col-12">
                                <input type="text" className="form-control" required onChange={(e) => this.handleChange(e)} name="email" placeholder="Email" />
                            </div>
                        </div>
                        <div className="text-center">
                            <div className="btn btn-danger " onClick={(e) => this.handleSubmit(e)}>ng k</div>
                        </div>
                    </div>
                </div>
            </form>
        )
    }
}

export default MailBox;