export const To_slug = (str) => {
    // Chuyn ht sang ch thng
    str = str.toLowerCase();

    // xa du
    str = str.replace(/(||||||||||||||||)/g, 'a');
    str = str.replace(/(||||||||||)/g, 'e');
    str = str.replace(/(||||)/g, 'i');
    str = str.replace(/(||||||||||||||||)/g, 'o');
    str = str.replace(/(||||||||||)/g, 'u');
    str = str.replace(/(||||)/g, 'y');
    str = str.replace(/()/g, 'd');

    // Xa k t c bit
    str = str.replace(/([^0-9a-z-\s])/g, '');

    // Xa khong trng thay bng k t -
    str = str.replace(/(\s+)/g, '-');

    // xa phn d -  u
    str = str.replace(/^-+/g, '');

    // xa phn d -  cui
    str = str.replace(/-+$/g, '');

    // return
    return str;
}
export const formatMoney = (t) => {
    return t.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
}
export const formatDate = (str) => {
    return str.split(',').slice(0, 1).join('');
}